import pinglun from './template/pinglun.vue'
import pinglunPanel from './template/pinglun-panel.vue';
import jyfParser from "./components/jyf-parser/jyf-parser";












export default {
	components: {
		pinglun,
		pinglunPanel,
		jyfParser,
	},
	data() {
		return {
			showPL: true,
			guanFlag: false,
			plList: [],
			info: '',
			noPingLun: false,
			zanFlag: false,
			shoucangFlag: false,
			contentImageList: [],
			shareData: {
				appId: 'wx1e1c7d0b7f8d179e',
				shareImg: 'http://h5.youzikankan.com/logo.png',
			},
			infoLoading:'http://h5.youzikankan.com/static/images/zw.png',
			shareFlag: false,
			reloadFlag: false,
			openAppShare: false,
			seeImageFlag: false,
			userLogin: true,
			viewList: [],
			pageDate: new Date(),
			zanAni: ''
		}
	},
	onShow() {
		var that = this;
		if (this.shareFlag) {
			// uni.showToast({
			// 	title: '分享成功',
			// 	duration: 800
			// })
			setTimeout(function() {
				that.shareFlag = false;
			}, 500)
		}
	},
	onNavigationBarButtonTap(event) {
		const buttonIndex = event.index;
		this.openAppShare = true;
		if (buttonIndex === 0) {
			// 分享 H5 的页面
		}
	},
	methods: {
		showZanAni() {
			let that = this;
			let arr = ['-', '']
			let arrImg = ['../../static/images/icon/dianzan-002.png']
			let randomImg = Math.floor(Math.random() * arrImg.length)
			let arrPush = {
				src: arrImg[randomImg], //用于随机图标
				index: false, //可删除
				time: 800, //动画时间
				animationData: {}, //每个盒子独立动画不可全局
				x: Math.ceil(Math.random() * 50), //方向间距
				q: Math.floor(Math.random() * arr.length), //用于随机方向
				lastIndex: 5, //用于删除数组
				timer: null, //定时器
				lastTime: 300 //删除的倒计时
			}
			//函数节流限制用户频繁快速点击
			if (new Date() - this.pageDate < 300) return //如果点击的时间小于初始时间300
			this.pageDate = new Date(); //同步全局时间
			//------------
			this.viewList.push(arrPush)

			//核心动画
			this.viewList.forEach((i, o, v) => {
				var animate = uni.createAnimation({
					duration: i.time,
					timingFunction: 'ease-out'
				})
				that.animation = animate
				let time = arr[i.q] + i.x //随机的方向和间距
				setTimeout(() => {
					that.animation.translateY(-80).step()
					that.animation.opacity(0).step()
					that.animation.translateX(Number(time)).step()
					i.animationData = that.animation.export()
					//函数防抖
					clearInterval(i.timer)
					i.timer = setInterval(() => {
						i.lastIndex-- //每个图标的倒计时到0删除
						if (i.lastIndex < 0) {
							v.splice(i, 1)
							clearInterval(i.timer)
						}
					}, i.lastTime)
				}, 100)
			})
		},
		openDown() {
			// window.Location.href="http://h5.youzikankan.com/h5/appFile/yzkk.apk"
			this.$commonJs.uniMsg('未上线app应用')
		},
		openH5() {
			uni.switchTab({
				url: '../../tabbar/index'
			})
		},
		/**
		 * @param {Object} i 0：微信聊天   1：朋友圈
		 */
		onShare(i) {
			// app端分享















		},
		shareWx_h5_init() {
			var that = this;
			this.$apiJs.getSignPackage({
				url: location.href,
			}).then(res => {
				this.shareData.appId = res.appId;
				//data是微信配置信息，option是分享要配置的内容
				wxapi.wxRegister(res, {
					title: that.info.title,
					link: res.url,
					imgUrl: that.info.img.length ? that.info.img[0].url : '',
					desc: that.info.desc,
					success: function() {
						that.shareFlag = true;
					},
					error: function() {
						console.log('分享error');
					}
				});
			})
		},
		linkPress(obj) {
			obj.ignore(
				uni.navigateTo({
					url: `../../package_other/other/webview?url=${obj.href}`
				})
			)
		},
		parserLoad() {},
		getInfo(id) {
			var that = this;
			this.$apiJs.getNewsInfo({
				id: id
			}).then(res => {
				var r_data = res;
				r_data.time = that.$commonJs.timeFormat.noYearFormat(r_data.create_time);
				var htmlString = r_data.content.content.replace(new RegExp('<video', "gm"), '<video width="100%"').replace(new RegExp(
					'style="', "gm"), 'style="max-width:100% !important;height:auto;').replace(new RegExp(
					'text-indent:28pt;"><video', "gm"), '"><video').replace(new RegExp('<img', "gm"), '<img width="100%"').replace(
					new RegExp(
						'style="', "gm"), 'style="max-width:100% !important;height:auto;').replace(new RegExp(
					'text-indent:2em;"><img', "gm"), '"><img').replace(new RegExp(
						'<iframe', "gm"),
					'<iframe style="border:0;width:auto;height:auto;" allowtransparency="yes"  width="auto" height="auto" frameborder="no" scrolling="no"  allowfullscreen'
				).replace(new RegExp(
					'<embed', "gm"), '<audio controlsList="nodownload" controls style="outline:none;height:40px;"').replace(new RegExp(
					'embed', "gm"), 'audio')
				r_data.desc = r_data.content.content.replace(/<[^>]+>/g, "").replace(/&nbsp;/ig, "").substring(0, 50);
				r_data.content = htmlString;
				that.info = r_data;
				that.setTitle("游子看看");
				if (uni.getStorageSync("accesstoken")) {
					this.getFollowCode();
				}
			}).then(() => {



			})
		},
		showErrorImage(e) {
			const index = e.target.dataset.index;
			this.info.user_img = this.$errorImage;
		},
		// 给新闻收藏
		shoucangNew() {
			var that = this;
			this.$commonJs.isLogin().then(res => {
				this.$apiJs.setCollectionNews({
					news_id: that.id,
				}).then(res => {
					this.getCollectionNews()
				})
			})
		},
		// 获取新闻收藏状态
		getCollectionNews() {
			var that = this;
			return new Promise((resolve, reject) => {
				this.$apiJs.getCollectionNews({
					id: that.id,
				}).then(res => {
					if (res != 'seeOther') {
						this.shoucangFlag = Number(res);
					} else {
						this.userLogin = false;
					}
				})
			})
		},
		huifu(i) {
			if (this.userLogin) {
				if (i.plNum) {
					this.navTo('plInfo', i);
				} else {
					console.log('弹出评论面板');
					this.otherUser = i.user_id; //被回复的人
					this.pl_id = i.id; //被回的id
					this.$refs.plPanel.showPl();
				}
			} else {
				this.$commonJs.toLoginPage()
			}
		},
		openPl() {
			if (this.userLogin) {
				this.$refs.plPanel.showPl();
			} else {
				this.$commonJs.toLoginPage()
			}
		},
		// 给新闻点赞
		dianzanNew() {
			var that = this;
			this.$commonJs.isLogin().then(res => {
				this.$apiJs.setFabulousNews({
					news_id: that.id,
				}).then(res => {
					this.getFabulousNewsCode()
					this.showZanAni();
				})
			})
		},
		// 获取新闻点赞状态
		getFabulousNewsCode() {
			var that = this;
			return new Promise((resolve, reject) => {
				this.$apiJs.getFabulousNewsCode({
					id: that.id,
				}).then(res => {
					if (res != 'seeOther') {
						this.zanFlag = res;
					}
				})
			})
		},
		// 关注该作者
		setFollow() {
			var that = this;
			this.$commonJs.isLogin().then(res => {
				this.$apiJs.setFollow({
					author_id: that.info.author_id,
				}).then(res => {
					this.getFollowCode()
				})
			})
		},
		// 获取关注状态
		getFollowCode() {
			var that = this;
			return new Promise((resolve, reject) => {
				this.$apiJs.getFollowCode({
					id: that.info.author_id,
				}).then(res => {
					this.guanFlag = res;
				})
			})
		},
		// 给评论点赞
		dianzan(i) {
			var that = this;
			var zanid = i.id;
			this.$commonJs.isLogin().then(res => {
				this.$apiJs.setFabulousUser({
					news_id: that.id,
					to_user_id: i.user_id,
					comment_id: zanid,
				}).then(res => {
					// this.getFabulousUserCode()
					this.getPingLun();
				})
			})
		},
		// 获取评论点赞状态 
		getFabulousUserCode() {
			var that = this;
			return new Promise((resolve, reject) => {
				this.$apiJs.getFabulousUserCode({
					id: that.id,
				}).then(res => {
					this.zanFlag = res;
				})
			})
		},
		submitPl(c) {
			var that = this;
			if (this.otherUser) {
				// 回复评论
				this.$apiJs.replyComment({
					news_id: that.id,
					content: c,
					author_id: this.otherUser,
					content_id: this.pl_id,
				}).then(res => {
					console.log(res);
					if (res) {
						this.$commonJs.uniMsg('回复成功');
						this.$refs.plPanel.closePl();
						this.getPingLun();
					}
				})
			} else {
				// 普通评论
				this.$apiJs.setComment({
					news_id: that.id,
					content: c,
					author_id: this.info.author_id,
				}).then(res => {
					console.log(res);
					if (res) {
						this.$commonJs.uniMsg('评论成功');
						this.$refs.plPanel.closePl();
						this.getPingLun();
					}
				})
			}
		},
		// 评论列表
		getPingLun() {
			var that = this;

			this.$apiJs.getLoginComment({
				id: that.id,
			}).then(res => {
				if (res != 'seeOther') {
					var r_data = res;
					if (r_data.length) {
						res.map((item, index) => {
							item.time = that.$commonJs.timeFormat.noYearFormat(item.create_time);
						})
						this.plList = res;
					} else {
						this.noPingLun = true;
					}
				} else {
					this.$apiJs.getComment({
						id: that.id,
					}).then(res => {
						if (res.length) {
							var r_data = res;
							res.map((item, index) => {
								item.time = that.$commonJs.timeFormat.noYearFormat(item.create_time);
							})
							this.plList = res;
						} else {
							this.noPingLun = true;
						}
					})
				}
			})
		},
		openPingLunInfo(i) {
			uni.navigateTo({
				url: './plInfo?new_id=' + this.id + '&author_id=' + i.user_id + '&content_id=' + i.id + ''
			})
		},
		navTo(url, param) {
			this.$commonJs.navTo(url + '?id=' + param.name + '')
		},
		setTitle(t) {
			uni.setNavigationBarTitle({
				title: t
			})
		},
	},
	onLoad(option) {
		this.id = option.id;







		this.getInfo(option.id);
		this.getPingLun(option.id);
		if (!option.from) {
			this.getFabulousNewsCode();
			this.getCollectionNews();
		}
		// 如果是分享过来的，把评论隐藏
		if (option.from) {
			this.showPL = false;
		}
	}
}
